<template>
  <div class="chatTxts">
    <div v-html="text"></div>
  </div>
</template>

<script>
export default {
  name: 'NormalMessage',
  props: {
    data: {},
  },
  computed: {
    text() {
      // line break to
      console.log('message', this.data);
      return this.data.message ? this.$commonHandler.parseLineBreakToBr(this.$commonHandler.linkify(this.data.message)) : '';
    },
  },
};
</script>